import React, { useState, useEffect } from 'react';
import { Row, Col, Card, ProgressBar, Button, Alert } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import ModuleService from '../services/ModuleService';
import ProgressService from '../services/ProgressService';
import SecuritySkillsOverview from '../components/dashboard/SecuritySkillsOverview';
import DashboardStats from '../components/dashboard/DashboardStats';

// Import dashboard styles
import '../styles/dashboard.css';

const Dashboard = () => {
  const [moduleProgress, setModuleProgress] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const { currentUser } = useAuth();
  const navigate = useNavigate();

  useEffect(() => {
    const fetchUserProgress = async () => {
      try {
        setIsLoading(true);
        setError(null);
        
        // Fetch user progress data
        const progressData = await ProgressService.getUserProgress(currentUser.id);
        setModuleProgress(progressData.progress);
      } catch (err) {
        console.error('Error fetching user progress:', err);
        setError('Failed to load your progress data. Please try again later.');
      } finally {
        setIsLoading(false);
      }
    };

    if (currentUser) {
      fetchUserProgress();
    }
  }, [currentUser]);

  const handleContinueModule = (moduleId) => {
    navigate(`/modules/${moduleId}`);
  };

  const handleStartModule = (moduleId) => {
    navigate(`/modules/${moduleId}`);
  };

  return (
    <>
      <div className="mb-4">
        <h2>Welcome{currentUser?.first_name ? `, ${currentUser.first_name}` : ''}!</h2>
        <p className="text-muted">
          Learn about web application security through interactive modules and hands-on exercises.
        </p>
      </div>

      {error && (
        <Alert variant="danger">{error}</Alert>
      )}

      {/* Dashboard Stats */}
      {!isLoading && moduleProgress.length > 0 && (
        <DashboardStats moduleProgress={moduleProgress} />
      )}
      
      {/* Security Skills Overview Widget */}
      {!isLoading && moduleProgress.length > 0 && (
        <section className="mb-5">
          <SecuritySkillsOverview moduleProgress={moduleProgress} />
        </section>
      )}

      <section className="mb-5">
        <h3 className="mb-4">Your Progress</h3>
        
        {isLoading ? (
          <p>Loading your progress...</p>
        ) : moduleProgress.length === 0 ? (
          <Alert variant="info">
            You haven't started any modules yet. Explore available modules below to begin learning.
          </Alert>
        ) : (
          <Row xs={1} md={2} lg={3} className="g-4">
            {moduleProgress.map((module) => (
              <Col key={module.module.id}>
                <Card className="h-100 module-progress-card">
                  <Card.Body>
                    <div className="mb-2">
                      <span className={`badge bg-${getBadgeColor(module.module.difficulty)}`}>
                        {module.module.difficulty}
                      </span>
                    </div>
                    <Card.Title>{module.module.name}</Card.Title>
                    <Card.Text className="text-muted small">
                      {module.total_lessons} lessons
                    </Card.Text>
                    
                    <div className="mt-2 mb-3">
                      <div className="d-flex justify-content-between mb-1">
                        <small>Progress</small>
                        <small>
                          {module.completed_lessons} / {module.total_lessons} lessons
                        </small>
                      </div>
                      <ProgressBar 
                        now={module.completion_percentage} 
                        variant={module.completion_percentage === 100 ? 'success' : 'primary'} 
                      />
                    </div>
                    
                    <Button 
                      variant="primary" 
                      className="w-100"
                      onClick={() => handleContinueModule(module.module.id)}
                    >
                      {module.completion_percentage === 100 ? 'Review Module' : 'Continue'}
                    </Button>
                  </Card.Body>
                </Card>
              </Col>
            ))}
          </Row>
        )}
      </section>

      <section>
        <div className="d-flex justify-content-between align-items-center mb-4">
          <h3 className="mb-0">Explore Modules</h3>
          <Button 
            variant="outline-primary" 
            size="sm"
            onClick={() => navigate('/modules')}
          >
            View All
          </Button>
        </div>
        
        <RecommendedModules 
          userProgress={moduleProgress} 
          onStartModule={handleStartModule} 
        />
      </section>
    </>
  );
};

// Recommended Modules component
const RecommendedModules = ({ userProgress, onStartModule }) => {
  const [modules, setModules] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchModules = async () => {
      try {
        setIsLoading(true);
        setError(null);
        
        // Fetch list of active modules
        const modulesData = await ModuleService.getModules();
        
        // Filter out modules that already exist in user progress
        const userModuleIds = userProgress.map(p => p.module.id);
        
        // Get recommended modules (not started or with different difficulty levels)
        let recommendedModules = modulesData.items.filter(
          module => !userModuleIds.includes(module.id)
        );
        
        // Limit to 3 modules for the dashboard view
        recommendedModules = recommendedModules.slice(0, 3);
        
        setModules(recommendedModules);
      } catch (err) {
        console.error('Error fetching modules:', err);
        setError('Failed to load recommended modules. Please try again later.');
      } finally {
        setIsLoading(false);
      }
    };
    
    fetchModules();
  }, [userProgress]);

  if (isLoading) {
    return <p>Loading recommended modules...</p>;
  }

  if (error) {
    return <Alert variant="danger">{error}</Alert>;
  }

  if (modules.length === 0) {
    return (
      <Alert variant="info">
        You've already started all available modules. Check your progress section above.
      </Alert>
    );
  }

  return (
    <Row xs={1} md={2} lg={3} className="g-4">
      {modules.map((module) => (
        <Col key={module.id}>
          <Card className="h-100">
            <Card.Body>
              <div className="mb-2">
                <span className={`badge bg-${getBadgeColor(module.difficulty)}`}>
                  {module.difficulty}
                </span>
              </div>
              
              <Card.Title>{module.name}</Card.Title>
              <Card.Text>
                {module.description}
              </Card.Text>
            </Card.Body>
            <Card.Footer className="bg-white">
              <Button 
                variant="outline-primary" 
                className="w-100"
                onClick={() => onStartModule(module.id)}
              >
                Start Module
              </Button>
            </Card.Footer>
          </Card>
        </Col>
      ))}
    </Row>
  );
};

// Helper function to get badge color based on difficulty
const getBadgeColor = (difficulty) => {
  switch (difficulty.toLowerCase()) {
    case 'beginner':
      return 'success';
    case 'intermediate':
      return 'warning';
    case 'advanced':
      return 'danger';
    default:
      return 'primary';
  }
};

export default Dashboard;
